body {
    padding : 0;
    margin: 0;
}

.container {
    width: 1120px;
    margin: auto;
}
.table {
    width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
    
    .textCenter {
        text-align: center;
    }
}
nav {
    margin-top:20px;
}
.navLink{
    a {
        
        padding : 0px 5px;
    }
    font-size: 20px;
    
    cursor: pointer;
    text-decoration: none;
    padding-bottom: 2px;
    
    color: #222;

    &:hover {
        color: #22b8cf;
    }

    &:after {
        content: '|';
        display: inline-block;
        padding: 0 7px;
        color: #ccc;
    }

    &:last-child {
        &:after {
            color: #fff;
        }
    }

    &.active {
        text-decoration: underline;
        color: #22b8cf;
        &:after {
            border-bottom: 4px solid #fff !important;
        }
    }
}
ul {
    padding : 0;
    margin: 25px auto;
    list-style: none;
    

    li {
        border-top: 1px solid #eee;
        width : 100%;
        display : table;

        a {
            display: block;
            padding: 0 5px;
            display : table-cell;
            text-decoration: none;
            
            position: relative;
            height: 150px;
            color : #222;
            padding-left: 170px;
            
        }
    }

    

    .mediaImg {
        width: 150px;
        height: 150px;
        position: absolute;
        left : 0;
        top : 0;
        object-fit: cover;
        
    }

}
 
